<template>
    <header>
        <div class="nav-bar">
            <div class="row">
                <div class="col-md-2 col-sm-6 col-xs-6">
                    <div class="logo-wrapper"><router-link to="/" class="logo"><img src="../../img/logo.png" alt=""></router-link></div>
                </div>
                <div id="nav" class="col-md-8 col-sm-12">
                    <div class="nav-wrapper">
                        <router-link tag="li" :to="nav.url" class="active font-12" v-for="nav in navs"><a>{{nav.title}}</a></router-link>
                    </div>
                </div>
                <div class="col-md-2 col-sm-6 col-xs-6">
                    <div class="mobile-menu-btn font-20">
                        <span>MENY</span>
                        <span class="glyphicon glyphicon-menu-hamburger"></span>
                    </div>
                    <div class="mobile-menu-btn hidden font-20">
                        <span>LUKK</span>
                        <i class="fa fa-close"></i>
                    </div>
                    <router-link to="/login"  class="blue-btn shadowed my-btn login-btn font-12">F ET TILBUD</router-link>
                </div>
            </div>
        </div>
        <div class="market-info-bar">
            <div class="row full-width">
                <div class="col-md-1"></div>
                <div class="col-md-10 col-sm-12">
                    <div class="market-info-wrapper row">
                        <li class="market-info" v-for="info in info">
                            <img src="../../img/icons/check-icon.png" alt="">
                            <span class="font-12">{{info}}</span>
                        </li>
                    </div>
                </div>
                <div class="col-md-1"></div>
            </div>
        </div>
        <div class="mobile-menu hidden">
            <div class="nav-wrapper">
                <li class="active font-22" v-for="nav in navs"><router-link v-bind:to="nav.url">{{nav.title}}</router-link></li>
                <button class="blue-btn shadowed my-btn login-btn font-20">F ET TILBUD</button>
            </div>
        </div>
    </header>
</template>

<script>
    $(document).ready(function() {
        $(".mobile-menu-btn").click(function(){
            $(".mobile-menu-btn").toggleClass("hidden");
            $(".mobile-menu").toggleClass("hidden");
            $("body").toggleClass("fixed-screen");
        });
    });
    export default {
        data(){
            return{
                navs: [
                        { title:'HJEM', url: '/'},
                        { title:'MERKER', url: '/brand'},
                        { title:'CAMPINGVOGN', url: '/caravan'},
                        { title:'BOBIL', url: '/RV'},
                        { title:'FINANSIERING', url: '#'},
                        { title:'OM OSS', url: '/about'},
                        { title:'KUNDESERVICE', url: '#'}
                    ],
                info:['Verkstedgodkjent', 'Opptil 5 rsgaranti ', 'Nye og brukte enehter', 'Gode finansieringsmuligheter ', 'Vi er her etter handelen']
            }
        },

        methods: {
            toStep2(){
                this.$router.push({path :'/step2'});
            }
        }


    }
</script>




