//import the vue instance
import Vue from 'vue'
//import the App component
import App from './App'
//import the vue router
import VueRouter from 'vue-router'
import Meta from 'vue-meta'

import VueClipboard from 'vue-clipboard2'
Vue.use(VueClipboard)

import  { store }  from './store/';

//tell vue to use the router
Vue.use(VueRouter)
Vue.use(Meta)
/* eslint-disable no-new */

import VueResourse from 'vue-resource';
Vue.use(VueResourse);

import VeeValidate from 'vee-validate';
Vue.use(VeeValidate);

import Home from './components/Home'
import brand from './components/brand'
import about from './components/about'
import login from './components/login'
import logout from './components/logout'
import caravan from './components/caravan'
import RV from './components/RV'
import single_caravan from './components/single_caravan'
import single_RV from './components/single_RV'
import single_brand from './components/single_brand'
import admin_sider from './components/admin_sider'
import admin_bobiler from './components/admin_bobiler'
import admin_campingvogn from './components/admin_campingvogn'
import rediger_side from './components/rediger_side'
import edit_products from './components/editProducts'
import admin_innstillinger from './components/admin_innstillinger'

import headerCommon from './components/headerCommon'
import footerCommon from './components/footerCommon'
import adminHeader from './components/adminHeader'
//define your routes
const routes = [
	{
		name: 'Home',
		path: '/',
		components: {
			default: Home,
			header: headerCommon,
			footer: footerCommon
		}
	},
	{
		name: 'brand',
		path: '/brand',
		components: {
			default: brand,
			header: headerCommon,
			footer: footerCommon
		}
	},
	{
		name: 'about',
		path: '/about',
		components: {
			default: about,
			header: headerCommon,
			footer: footerCommon
		}
	},
	{
		name: 'single_brand',
		path: '/single_brand',
		components: {
			default: single_brand,
			header: headerCommon,
			footer: footerCommon
		}
	},
	{
		name: 'single_caravan',
		path: '/single_caravan',
		components: {
			default: single_caravan,
			header: headerCommon,
			footer: footerCommon
		}
	},
	{
		name: 'single_RV',
		path: '/single_RV',
		components: {
			default: single_RV,
			header: headerCommon,
			footer: footerCommon
		}
	},
	{
		name: 'login',
		path: '/login',
		component: login
	},
	{
		name: 'logout',
		path: '/logout',
		component: logout
	},
	{
		name: 'caravan',
		path: '/caravan',
		components: {
			default: caravan,
			header: headerCommon,
			footer: footerCommon
		}
	},{
		name: 'RV',
		path: '/RV',
		components: {
			default: RV,
			header: headerCommon,
			footer: footerCommon
		}
	},
	{
		name: 'admin_sider',
		path: '/admin_sider',
		components: {
			default: admin_sider,
			header: adminHeader
		}
	},
	{
		name: 'admin_bobiler',
		path: '/admin_bobiler',
		components: {
			default: admin_bobiler,
			header: adminHeader
		}
	},
	{
		name: 'admin_campingvogn',
		path: '/admin_campingvogn',
		components: {
			default: admin_campingvogn,
			header: adminHeader
		}
	},
	{
		name: 'rediger_side',
		path: '/rediger_side',
		components: {
			default: rediger_side,
			header: adminHeader
		}
	},
	{
		name: 'edit_products',
		path: '/edit_products',
		components: {
			default: edit_products,
			header: adminHeader
		}
	},
	{
		name: 'admin_innstillinger',
		path: '/admin_innstillinger',
		components: {
			default: admin_innstillinger,
			header: adminHeader
		}
	}
]

// Create the router instance and pass the `routes` option
// You can pass in additional options here, but let's
// keep it simple for now.
const router = new VueRouter({
	routes, // short for routes: routes
	mode: 'history'
})



//instatinat the vue instance
new Vue({
	//define the selector for the root component
	el: '#app',
	//pass the template to the root component
	template: '<App/>',
	//declare components that the root component can access
	components: { App },
	//pass in the router to the vue instance
	router,
	store
}).$mount('#app')//mount the router on the app



