<template>
  	<div class="col-md-10 col-sm-12 sider-wrapper">
		<div class="title-bar row">
			<span class="font-25">Bobiler</span>
			<div class="avarta">
				<span class="font-10">JAN-BRRE HHEIM</span>
				<img src="../../img/icons/avarta.png" alt="">
			</div>
		</div>
		<div class="table">
			<div class="row">
				<div class="col-md-3 col-sm-12">
					<form>
						<div class="input-group">
							<input type="text" class="form-control" placeholder="Search" v-model="query">
							<div class="input-group-btn">
							<button class="btn btn-default" type="button" @click="find()">
								<i class="glyphicon glyphicon-search"></i>
							</button>
							</div>
						</div>
					</form>
				</div>
				<div class="col-md-2 col-sm-12">
					<div class="form-group">
						<select class="form-control" id="sel1">
							<option>Challenger</option>
							<option>2</option>
							<option>3</option>
							<option>4</option>
						</select>
					</div>
				</div>
			</div>
			<div class="table-header col-lg-12 col-md-12">
				<div class="col-md-6 col-sm-12">
					<span>Bobil modell</span>
				</div>
				<div class="col-md-2 col-sm-12">
					<span>Merke</span>
				</div>
				<div class="col-md-2 col-sm-12">
					<span>Arsmodell</span>
				</div>
				<div class="col-md-2 col-sm-12">
					<span>Soveplasser</span>
				</div>
			</div>
			<div class="table-row col-lg-12 col-md-12" v-for="product in filteredproducts">
				<div class="cell col-md-6 col-sm-12">
					<span>{{product.name}}</span>
					<ul class="edit">
						<li @click="editOne(product.adId)">Rediger</li>
						<li @click="deleteOne(product.adId)">Slett</li>
					</ul>
				</div>
				<div class="cell col-md-2 col-sm-12">
					<span class="field">Merke</span>
					<span>{{ product.adata.make}}</span>
				</div>
				<div class="cell col-md-2 col-sm-12">
					<span class="field">Arsmodell</span>
					<span>{{ product.adata.year}}</span>
				</div>
				<div class="cell col-md-2 col-sm-12">
					<span class="field">Soveplasser</span>
					<div>{{ product.adata.seats}}</div>
				</div>
				<div class="cell">
				</div>
			</div>			
		</div>
	</div>
</template>


<script>
// import {mapGetters} from 'vuex';
import axios from 'axios';

export default {
  data(){
		return{
			'query':'',
			'products':[],
			'filteredproducts' :[],
			'message':'',
			'showMessage':false
		}
	},
	methods: {
		find(){
			// this.filteredproducts = this.products.filter((age)=> {
   //  		return age.name.toLowerCase().indexOf(this.query.toLowerCase()) !== -1;
			// });
		},
		editOne(id) {  
			console.log('editing ');
			this.$store.dispatch('getProductData', id);
			// setTimeout(function(){ 
				this.$router.push({path :'/edit_products'});
			// }, 300);
		},
		deleteOne(id){
			var self = this;
			this.$store.dispatch('deleteOne',id)
                .then((data)=>{
                    if(data){
                  		for(var i = 0; i < self.products.length; i++) {
											    if(self.products[i].adId == id) {
											        self.products.splice(i, 1);
											        self.filteredproducts = self.products;											        
											        break;
											    }
											}
                    } else{

                    }        
                })
                .catch((error)=> {
                    console.log('error:',error);           
                });
		}
	},

	created() {
		var self=this;
		this.$store.dispatch('getRV')
                .then((data)=>{
                    if(data){
                        self.products = data;
                				self.filteredproducts = data;
                    } else{

                    }        
                })
                .catch((error)=> {
                    console.log('error:',error);           
                });   
	}
}
	
</script>



